/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable @next/next/no-img-element */
import { useState, useEffect } from "react";
import { useRouter } from "next/router";
import { CloseCircleOutlined, SaveOutlined } from "@ant-design/icons";
import { Button, Card, Col, Row, Tabs } from "antd";
import {
  CEditAssetInput,
  CEditAssetImage,
  CEditAssetAttachments,
  CTitle,
  CPageTitle,
  ErrorNotification,
  SuccesNotification,
} from "@components";
import CApi from "@api";

const { TabPane } = Tabs;
const EditAudit = () => {
  useEffect(() => {
    getListMaster();
    getData();
  }, []);
  const router = useRouter();
  const [departement, setDepartement] = useState({ dataDepartement: [] });
  const [listLocation, setListLocation] = useState({ dataLocation: [] });
  const [listBranch, setListBranch] = useState({ dataBranch: [] });
  const [listStatus, setListStatus] = useState({ dataStatus: [] });
  const [listAssetType, setListAssetType] = useState({ dataAssetType: [] });
  const [listCodeActiva, setListCodeActiva] = useState({ dataCodeActiva: [] });
  const [listAnggaran, setListAnggaran] = useState({ dataAnggaran: [] });
  const [listOwnership, setListOwnership] = useState({ dataOwnership: [] });
  const [isSubmit, setIsSubmit] = useState(false);
  const [isImageDua, setIsImageDua] = useState(false);
  const [imageUrlSatu, setImageUrlSatu] = useState("");
  const [error, setError] = useState("");
  const [image, setImage] = useState(false);
  const [loading, setLoading] = useState(false);
  const [imageUrlDua, setImageUrlDua] = useState("");
  const [loadingDua, setLoadingDua] = useState(false);
  const [idImageSatu, setIdImageSatu] = useState();
  const [idImageDua, setIdImageDua] = useState();
  const [attachmentsID, setAttachmentsID] = useState();
  const [attachmentsID2, setAttachmentsID2] = useState();
  const [attachmentsID3, setAttachmentsID3] = useState();
  const [fileNameSatu, setFileNameSatu] = useState("Choose File...");
  const [fileNameDua, setFileNameDua] = useState("Choose File...");
  const [fileNameTiga, setFileNameTiga] = useState("Choose File...");
  const [forms, setForms] = useState({
    id: "",
    tag_number: "",
    old_number: "",
    name: "",
    asset_category_id: "",
    trademark: "",
    merek_barang: "",
    model: "",
    color: "",
    serial_number: "",
    part_number: "",
    anggaran_id: "",
    qty: "",
    invoice_number: "",
    value: "",
    lifetime: "",
    year: "",
    location_id: "",
    unit_id: "",
    uselife: "",
    type: "",
    vendor_id: "",
    status_id: "",
    branch_id: "",
    remark: "",
    picture: "",
    no_barang: "",
    flag: "",
    asset_type_id: "",
    code_activa_id: "",
    ownership_id: "",
    file_name_satu: "",
    file_description_satu: "",
    file_name_dua: "",
    file_description_dua: "",
    file_name_tiga: "",
    file_description_tiga: "",
    tgl_perolehan: "",
    payment_voucher: "",
    harga_perolehan: "",
    currency_perolehan: "",
  });

  // getData List Option
  const getListMaster = async () => {
    await CApi.get("api/list-master").then((res) => {
      setDepartement({
        ...departement,
        dataDepartement: res.data.vendorList,
      });
      setListStatus({
        ...listStatus,
        dataStatus: res.data.statusList,
      });
      setListLocation({
        ...listLocation,
        dataLocation: res.data.location,
      });
      setListBranch({
        ...listBranch,
        dataBranch: res.data.branchList,
      });
      setListAssetType({
        ...listAssetType,
        dataAssetType: res.data.assetType,
      });
      setListCodeActiva({
        ...listCodeActiva,
        dataCodeActiva: res.data.codeActiva,
      });
      setListOwnership({
        ...listOwnership,
        dataOwnership: res.data.ownership,
      });
      setListAnggaran({
        ...listAnggaran,
        dataAnggaran: res.data.anggaran,
      });
    });
  };
  // Last getData List Option

  const handleChange = (evt) => {
    const newData = { ...forms };
    newData[evt.target.id] = evt.target.value;
    setForms(newData);
  };

  const onChangeDate = (value, dateString) => {
    setForms({
      ...forms,
      uselife: dateString,
    });
  };
  const onChangeDatePerolehan = (value, dateString) => {
    setForms({
      ...forms,
      tgl_perolehan: dateString,
    });
  };

  const onRemark = ({ target: { value } }) => {
    setForms({
      ...forms,
      remark: value,
    });
  };
  const onDescriptionSatu = ({ target: { value } }) => {
    setForms({
      ...forms,
      file_description_satu: value,
    });
  };
  const onDescriptionDua = ({ target: { value } }) => {
    setForms({
      ...forms,
      file_description_dua: value,
    });
  };
  const onDescriptionTiga = ({ target: { value } }) => {
    setForms({
      ...forms,
      file_description_tiga: value,
    });
  };

  // Fungsi Select Option

  function onBranch(value) {
    setForms({
      ...forms,
      branch_id: value,
    });
  }
  function onLocation(value) {
    setForms({
      ...forms,
      location_id: value,
    });
  }
  function onDepartement(value) {
    setForms({
      ...forms,
      vendor_id: value,
    });
  }
  function onStatus(value) {
    setForms({
      ...forms,
      status_id: value,
    });
  }
  function onAssetType(value) {
    setForms({
      ...forms,
      asset_type_id: value,
    });
  }
  function onCodeActiva(value) {
    setForms({
      ...forms,
      code_activa_id: value,
    });
  }
  function onOwnersip(value) {
    setForms({
      ...forms,
      ownership_id: value,
    });
  }
  function onCurrency(value) {
    setForms({
      ...forms,
      currency_perolehan: value,
    });
  }

  function onActiva(value) {
    setForms({
      ...forms,
      status_activa: value,
    });
  }

  function onAnggaran(value) {
    setForms({
      ...forms,
      anggaran_id: value,
    });
  }
  function onFlag(value) {
    setForms({
      ...forms,
      flag: value,
    });
  }
  // Akhir Fungsi Select Option

  const handlePickImage = () => {
    const f = document.getElementById("select-image");
    f.click();
  };
  const handlePickImage2 = () => {
    const f = document.getElementById("select-image-2");
    f.click();
  };

  const toBase64 = (file) =>
    new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = (error) => reject(error);
    });

  const onPickImage = (file) => {
    setLoading(true);
    toBase64(file).then((res) => {
      const type = "data:" + file.type + ";base64,";
      const valueBase64 = res.replaceAll(type, "");
      if (idImageSatu == undefined || null) {
        setForms({
          ...forms,
          imageSatu: {
            name: file.name,
            path: res,
            type: type,
            base64: valueBase64,
          },
        });
      } else {
        setForms({
          ...forms,
          imageSatu: {
            id: idImageSatu,
            name: file.name,
            path: res,
            type: type,
            base64: valueBase64,
          },
        });
      }
      setImageUrlSatu(res);
      setImage(true);
      setLoading(false);
      setTimeout(() => {
        document.getElementById("select-image").value = "";
      }, 1000);
    });
  };
  const onPickImage2 = (file) => {
    setLoadingDua(true);
    toBase64(file).then((res) => {
      const type = "data:" + file.type + ";base64,";
      const valueBase64 = res.replaceAll(type, "");
      if (idImageDua == undefined || null) {
        setForms({
          ...forms,
          imageDua: {
            name: file.name,
            path: res,
            type: type,
            base64: valueBase64,
          },
        });
      } else {
        setForms({
          ...forms,
          imageDua: {
            id: idImageDua,
            name: file.name,
            path: res,
            type: type,
            base64: valueBase64,
          },
        });
      }
      setImageUrlDua(res);
      setIsImageDua(true);
      setLoadingDua(false);
      setTimeout(() => {
        document.getElementById("select-image-2").value = "";
      }, 1000);
    });
  };

  const onPickFile1 = async (file) => {
    toBase64(file).then((res) => {
      const name = file.name;
      const type = "data:" + file.type + ";base64,";
      const valueBase64 = res.replaceAll(type, "");
      const mime = file.type;
      setFileNameSatu(name);
      if (attachmentsID == undefined || null) {
        setForms({
          ...forms,
          attachments1: {
            name: name,
            type: type,
            base64: valueBase64,
            mime: mime,
          },
        });
      } else {
        setForms({
          ...forms,
          attachments1: {
            id: attachmentsID,
            name: name,
            type: type,
            base64: valueBase64,
            mime: mime,
          },
        });
      }
    });
  };
  const onPickFile2 = async (file) => {
    toBase64(file).then((res) => {
      const name = file.name;
      const type = "data:" + file.type + ";base64,";
      const valueBase64 = res.replaceAll(type, "");
      const mime = file.type;
      setFileNameDua(name);
      if (attachmentsID2 == undefined || null) {
        setForms({
          ...forms,
          attachments2: {
            name: name,
            type: type,
            base64: valueBase64,
            mime: mime,
          },
        });
      } else {
        setForms({
          ...forms,
          attachments2: {
            id: attachmentsID2,
            name: name,
            type: type,
            base64: valueBase64,
            mime: mime,
          },
        });
      }
    });
  };
  const onPickFile3 = async (file) => {
    toBase64(file).then((res) => {
      const name = file.name;
      const type = "data:" + file.type + ";base64,";
      const valueBase64 = res.replaceAll(type, "");
      const mime = file.type;
      setFileNameTiga(name);
      if (attachmentsID3 == undefined || null) {
        setForms({
          ...forms,
          attachments3: {
            name: name,
            type: type,
            base64: valueBase64,
            mime: mime,
          },
        });
      } else {
        setForms({
          ...forms,
          attachments3: {
            id: attachmentsID3,
            name: name,
            type: type,
            base64: valueBase64,
            mime: mime,
          },
        });
      }
    });
  };

  const getData = async () => {
    let id = router.query.id;
    await CApi.get("api/detail-update/" + id)
      .then((r) => {
        setForms(r.data);
        if (r.data.attachments1 != null) {
          setFileNameSatu(r.data.attachments1.name);
          setAttachmentsID(r.data.attachments1.id);
        }
        if (r.data.attachments2 != null) {
          setFileNameDua(r.data.attachments2.name);
          setAttachmentsID2(r.data.attachments2.id);
        }
        if (r.data.attachments3 != null) {
          setFileNameTiga(r.data.attachments3.name);
          setAttachmentsID3(r.data.attachments3.id);
        }
        if (r.data.imageSatu != null) {
          setIdImageSatu(r.data.imageSatu.id);
          setImageUrlSatu(r.data.imageSatu.ext + r.data.imageSatu.file);
          setImage(true);
        }
        if (r.data.imageDua != null) {
          setIdImageDua(r.data.imageDua.id);
          setImageUrlDua(r.data.imageDua.ext + r.data.imageDua.file);
          setIsImageDua(true);
        }
      })
      .catch((err) => {
        if ("statusText" in err.response && "message" in err.response.data) {
          ErrorNotification(err.response.statusText, err.response.data.message);
        }
        router.push("/audit");
      });
  };

  const handlePickFileSatu = () => {
    const f = document.getElementById("attachments-1");
    f.click();
  };
  const handlePickFileDua = () => {
    const f = document.getElementById("attachments-2");
    f.click();
  };
  const handlePickFileTiga = () => {
    const f = document.getElementById("attachments-3");
    f.click();
  };

  const handleSubmit = async () => {
    setIsSubmit(true);
    await CApi({
      method: "POST",
      url: "api/audit",
      data: forms,
    })
      .then((res) => {
        SuccesNotification(res.data.status, res.data.message);
        router.back();
      })
      .catch((err) => {
        if ("statusText" in err.response && "message" in err.response.data) {
          ErrorNotification(err.response.statusText, err.response.data.message);
        }
        if ("errors" in err.response.data) {
          setError(err.response.data.errors);
        }
      })
      .finally(() => {
        setIsSubmit(false);
      });
  };

  return (
    <Row>
      <CTitle title="Edit Asset" />
      <Col span={24}>
        <Card
          cover={
            <Col
              span={24}
              style={{ backgroundColor: "#1890ff", paddingTop: "5px" }}
            />
          }
        >
          <CPageTitle />
          <Tabs defaultActiveKey="1">
            <TabPane tab="Data" key="1">
              <CEditAssetInput
                forms={forms}
                handleChange={handleChange}
                listAssetType={listAssetType}
                onAssetType={onAssetType}
                error={error}
                onBranch={onBranch}
                listBranch={listBranch}
                onChangeDate={onChangeDate}
                onLocation={onLocation}
                listLocation={listLocation}
                onDepartement={onDepartement}
                departement={departement}
                onStatus={onStatus}
                listStatus={listStatus}
                onRemark={onRemark}
                listCodeActiva={listCodeActiva}
                onCodeActiva={onCodeActiva}
                listOwnership={listOwnership}
                onOwnersip={onOwnersip}
                onCurrency={onCurrency}
                onActiva={onActiva}
                listAnggaran={listAnggaran}
                onAnggaran={onAnggaran}
                onFlag={onFlag}
                onChangeDatePerolehan={onChangeDatePerolehan}
              />
            </TabPane>
            <TabPane tab="Photos" key="2">
              <CEditAssetImage
                image={image}
                handlePickImage={handlePickImage}
                imageUrl={imageUrlSatu}
                loading={loading}
                isImageDua={isImageDua}
                handlePickImage2={handlePickImage2}
                imageUrlDua={imageUrlDua}
                loadingDua={loadingDua}
                onPickImage={onPickImage}
                onPickImage2={onPickImage2}
              />
            </TabPane>
            <TabPane tab="Attachments" key="3">
              <CEditAssetAttachments
                onPickFile1={onPickFile1}
                onPickFile2={onPickFile2}
                onPickFile3={onPickFile3}
                forms={forms}
                handleChange={handleChange}
                onDescriptionSatu={onDescriptionSatu}
                onDescriptionDua={onDescriptionDua}
                onDescriptionTiga={onDescriptionTiga}
                fileName1={fileNameSatu}
                fileName2={fileNameDua}
                fileName3={fileNameTiga}
                handlePickFileSatu={handlePickFileSatu}
                handlePickFileDua={handlePickFileDua}
                handlePickFileTiga={handlePickFileTiga}
              />
            </TabPane>
          </Tabs>
          <Row style={{ float: "right" }} gutter={5}>
            <Col>
              <Button
                icon={<CloseCircleOutlined />}
                style={{
                  backgroundColor: "#F0AD4E",
                  color: "#fff",
                  marginRight: 15,
                }}
                onClick={() => router.push("/asset")}
                disabled={isSubmit}
              >
                Cancel
              </Button>
              <Button
                icon={<SaveOutlined />}
                type="primary"
                onClick={() =>
                  handleSubmit(
                    "api/audit",
                    forms,
                    setError,
                    setIsSubmit,
                    router
                  )
                }
              >
                Save
              </Button>
            </Col>
          </Row>
        </Card>
      </Col>
    </Row>
  );
};

export default EditAudit;
